import React from "react";
import { BrowserRouter as Router, Link, Routes, Route } from 'react-router-dom';
import HomeWrapper from "./home.jsx";
import './navbar.css'; // Import CSS file for styling
import Map from "./Map.jsx";
import Gllary from "./gallary.jsx";
const NavBar = () => {
  return (
    <Router>
      <nav className="navbar navbar-expand-lg navbar-light bg-light">
        
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse justify-content-end" id="navbarNav">
          <ul className="navbar-nav">
            <li className="nav-item">
              <Link to="/" className="nav-link" activeClassName="active">
                Home
              </Link>
            </li>
            <li className="nav-item">
              <Link to="/gallary" className="nav-link" activeClassName="active">
                View images
              </Link>
            </li>
            <li className="nav-item">
              <Link to="/pricing" className="nav-link" activeClassName="active">
                Pricing
              </Link>
            </li>
            <li className="nav-item">
              <Link to="/map" className="nav-link" activeClassName="active">
                Map
              </Link>
            </li>
          </ul>
        </div>
      </nav>

      <Routes>
        <Route exact path="/" element={<HomeWrapper />} />
        {/* Define routes for other pages */}
        <Route path="/gallary" element={<Gllary />} />
           {/*  <Route path="/contact" element={<Contact />} />
            <Route path="/features" element={<Features />} />*/}
            <Route path="/map" element={<Map />} /> 
      </Routes>
    </Router>
  );
};

export default NavBar;
