import React, { useState, useRef } from 'react';
import ImgsViewer from 'react-images-viewer';
import './Home.css';

const HomeWrapper = () => {
  const fileInputRef = useRef(null);
  const [selectedImages, setSelectedImages] = useState([]);
  const [viewImages, setViewImages] = useState([]);
  const [isOpen, setIsOpen] = useState(false);
  const [currImg, setCurrImg] = useState(0);

  const handleFileChange = async (event) => {
    const files = event.target.files;
    const selected = [...selectedImages]; // Copy the existing array of selected images
    for (let i = 0; i < files.length; i++) {
      const imageInfo = await resizeImage(files[i]);
      selected.push(imageInfo);
    }
    setSelectedImages(selected);
  };

  const resizeImage = (file) => {
    return new Promise((resolve) => {
      const reader = new FileReader();
      reader.onload = (event) => {
        const img = new Image();
        img.onload = () => {
          const canvas = document.createElement('canvas');
          const ctx = canvas.getContext('2d');
          const maxWidth = 200; // Maximum width for resized image
          const maxHeight = 200; // Maximum height for resized image
          let width = img.width;
          let height = img.height;

          // Calculate new dimensions while maintaining aspect ratio
          if (width > height) {
            if (width > maxWidth) {
              height *= maxWidth / width;
              width = maxWidth;
            }
          } else {
            if (height > maxHeight) {
              width *= maxHeight / height;
              height = maxHeight;
            }
          }

          canvas.width = width;
          canvas.height = height;
          ctx.drawImage(img, 0, 0, width, height);

          // Convert canvas to data URL
          const resizedImageUrl = canvas.toDataURL('image/jpeg');

          // Resolve with the resized image URL and additional information
          resolve({
            src: resizedImageUrl,
            resolution: `${img.width}x${img.height}`,
            size: `${(file.size / 1024).toFixed(2)} KB`,
            type: file.type,
            dimensions: `${width}x${height}`,
            fileName: file.name,
            fileType: file.type
          });
        };
        img.src = event.target.result;
      };
      reader.readAsDataURL(file);
    });
  };

  const handleImageClick = (index) => {
    setCurrImg(index);
    setIsOpen(true);
  };

  const handleAddImage = () => {
    fileInputRef.current.click(); // Trigger file input when "+" button is clicked
  };

  const handleDone = () => {
    setViewImages([...selectedImages]); // Copy selected images to view images
    // Do not clear selected images when done button is clicked
  };

  const handleCloseImgsViewer = () => {
    setIsOpen(false);
  };

  return (
    <div className="home-wrapper" style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', textAlign: 'center', paddingTop: '180px' }}>
      <h1>IMPORT  YOUR  PHOTOS</h1>
      <p>Make sure they are JPG or HEIF format</p>
      {/* Hidden file input */}
      <input ref={fileInputRef} type="file" accept=".jpg,.heif" style={{ display: 'none' }} onChange={handleFileChange} multiple />
      {/* Display selected images */}
      <div className="selected-images">
        {selectedImages.map((image, index) => (
          <div key={index} className="image-container" onClick={() => handleImageClick(index)}>
            <img src={image.src} alt={`Selected Image ${index}`} />
            <div className="image-details">
              <p>Resolution: {image.resolution}</p>
              <p>Size: {image.size}</p>
              <p>Type: {image.type}</p>
              <p>Dimensions: {image.dimensions}</p>
              <p>File Name: {image.fileName}</p>
              <p>File Type: {image.fileType}</p>
            </div>
          </div>
        ))}
        {/* "+" button */}
        <button className="add-image-button" onClick={handleAddImage}>Add image</button>
      </div>
      {/* Done button (conditionally rendered) */}
      {selectedImages.length > 0 && (
        <button className="done-button" onClick={handleDone}>Done</button>
      )}
      {/* View images */}
      <ImgsViewer
        imgs={viewImages}
        isOpen={isOpen}
        currImg={currImg}
        onClickPrev={() => setCurrImg(currImg - 1)}
        onClickNext={() => setCurrImg(currImg + 1)}
        onClose={handleCloseImgsViewer}
      />
    </div>
  );
};

export default HomeWrapper;
